* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  @import url(//db.onlinewebfonts.com/c/f6c9f5fa5b05e0e1847d566236e2731f?family=Elektra+Text+Pro);
}
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 70px auto;
}

body {
  background-color: #d9d9d9;
}

.txt-area {
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  color: #666;
  width: 520px;
  height: 460px;
  border-radius: 3%;
  box-sizing: border-box;
  padding: 16px;
  transition: box-shadow 0.3s;
  background-color: white;
  resize: none;
  border: 1px solid #e6e6e6;
}

.txt-area:focus-visible {
  outline: none;
  box-shadow: 1px 1px 5px;
}

input {
  border: 1px solid white;
  background-color: #0b203b;
  color: white;
  width: 100px;
  height: 40px;
  transition: background-color 0.3s;
  top: 410px;
  right: 55em;
  cursor: pointer;
  border-radius: 8px;
}

input:hover {
  background-color: white;
  border: 1px solid #0b203b;
  color: #364459;
}

.display {
  display: inline-block;
  word-spacing: 2px;
}

.result {
  margin-right: 10px;
}
.show {
  margin: 60px auto;
  width: 600px;
  height: 500px;
}

.suggestion {
  background-color: #333;
  transition: color 3s ease-in-out;
  border-bottom: #666 1px solid;
  width: 160px;
  height: 25px;
  text-align: center;
  list-style: none;
  cursor: pointer;
  transition: font-size 0.2s;
}

.suggestion:hover {
  font-size: 18px;
}

.result .ul {
  visibility: hidden;
  margin-top: 0;
  display: block;
  position: fixed;
  color: white;
  font-family: "Open Sans", sans-serif;
  font-size: 17px;
}

.result:hover .ul {
  visibility: visible;
}
.hl {
  -webkit-animation: 1.5s highlight 1.5s 1 normal forwards;
  animation: 1.5s highlight 1.5s 1 normal forwards;
  background-color: none;
  background: linear-gradient(90deg, #7efff5 50%, rgba(255, 255, 255, 0) 50%);
  background-size: 200% 100%;
  background-position: 100% 0;
}

@-webkit-keyframes highlight {
  to {
    background-position: 0 0;
  }
}

@keyframes highlight {
  to {
    background-position: 0 0;
  }
}
.btn {
  width: 150px;
  height: 50px;
  margin-left: 600px;
  margin-top: -10px;
  background-color: #0b203b;
  color: whitesmoke;
  transition: background-color 0.3s;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 0.5em;
}

.btn:hover {
  background-color: white;
  color: #364459;
  text-align: center;
}

.cpy {
  height: 100px;
  width: 100px;
  text-align: center;
  color: #0b203b;
  cursor: pointer;
}

.about {
  margin: 250px auto;
  width: 45vw;
  height: 150px;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: #06162b;
  color: #d1cccc;
  border-radius: 12px;
}

.arrows {
  width: 70px;
  height: 35px;
  box-sizing: border-box;
  margin-inline: 20px;
  padding-top: 18px;
  text-align: center;
  top: 0;
  color: white;
  background-color: #0b203b;
  border-radius: 20px;
  cursor: pointer;
}

@media (max-width: 765px) {
  form {
    display: block;
    margin: 1px auto;
  }
  .txt-area {
    margin: 1em auto;
    height: 150px;
    width: 150%;
  }

  .result:active .ul {
    visibility: visible;
  }
}
